import {
    createApp
} from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import {
    Button,
    Search,
    Icon,
    Swipe,
    SwipeItem,
    Image,
    Tabbar,
    TabbarItem, Grid, GridItem,Calendar 
} from 'vant';
import "amfe-flexible";
const app = createApp(App)
document.title = "小李补胎微商城欢迎您"
app.use(store).use(router).use(Button).use(Icon).use(Search).use(Swipe).use(SwipeItem).use(Image).use(Tabbar).use(TabbarItem).use(Grid).use(GridItem).use(Calendar).mount('#app')